document.addEventListener("DOMContentLoaded", function () {
  detectarClicks();
  detectarDarkMode();
  eventListeners();
});

function detectarClicks() {
  const mobileMenu = document.querySelector(".mobile-menu"); //Al seleccionar un elemento tipo clase hace falta referirse con .  .mobile-menu
  mobileMenu.addEventListener("click", AbrirMenu);
  const darkMode = document.querySelector(".dark-mode");
  darkMode.addEventListener("click", ModoNoche);
}

function detectarDarkMode() {
  const prefiereModoNoche = window.matchMedia("(prefers-color-scheme: dark)");
  if (prefiereModoNoche.matches) {
    document.body.classList.add("modo-noche");
  } else {
    document.body.classList.remove("modo-noche");
  }

  prefiereModoNoche.addEventListener("change", function () {
    //En caso de que cambie a modo noche desde preferencias, no tendra que recargar la pagina
    if (prefiereModoNoche.matches) {
      document.body.classList.add("modo-noche");
    } else {
      document.body.classList.remove("modo-noche");
    }
  });
}

function eventListeners() {
  //Muestra campos condicionales
  const metodoContacto = document.querySelectorAll(
    'input[name="contacto[contacto]"]'
  ); //Selecciona todos los campos que compartan name="contacto[contacto]""
  metodoContacto.forEach((input) =>
    input.addEventListener("click", mostrarMetodosContacto)
  ); //Itinera en los seleccionados y le añade al un event lisener que si pulsamos click, ejecuta la funcion mostrarMetodosContacto
}

function mostrarMetodosContacto(e) {
  const mostrarDiv = document.querySelector('#contacto'); //Selecciona el div que tiene el id= contacto
  if(e.target.value === 'telefono'){
    mostrarDiv.innerHTML = `
    <label for="telefono">Número de telefono</label>
    <input data-cy="input-telefono" type="tel" placeholder="Tu telefono" id="telefono" name="contacto[telefono]" >

    <p>Fecha y hora para que te llamemos</p>

    <label for="fecha" >Fecha</label>
    <input data-cy="input-fecha" type="date" id="fecha" name="contacto[fecha]">
    
    <label for="hora" name="contacto[hora]">Hora</label>
    <input data-cy="input-hora" type="time" id="hora" min="09:00" max="18:00" name="contacto[hora]">
    `
  }else{ //InnerHTML inserta codigo html // `` es como una string pero nos permite poder codigo en diversas lineas para que se vea mejor
    mostrarDiv.innerHTML = `          
    <label for="email">Email</label>
    <input data-cy="input-email" type="email" placeholder="Tu email" id="email" name="contacto[email]" required>
    `
  }
}

function AbrirMenu() {
  const navegacion = document.querySelector(".navegacion");
  if (navegacion.classList.contains("mostrar")) {
    //Como aqui ya nos estamos refiriendo a una clase con .classlist, no hace falta poner el .
    navegacion.classList.remove("mostrar");
  } else {
    navegacion.classList.add("mostrar");
  }
}

function ModoNoche() {
  const body = document.body;
  body.classList.toggle("modo-noche");
}
